import modalComponent from '@/components/modalComponent.vue'
import btnComponent from '@/components/btnComponent.vue'
export default {
	components: {
		modalComponent,
		btnComponent
	},
	data() {
		return {
			requestCount: 1,

			user_id: null,

			spriteInfo: {},
			invitList: [],
			invitAward: '',
			modal: '',
			earnCuttime: 1, // 收益计时
			skillShow: false, // 显示技能

			userInfo: {},
		};
	},
	onShareAppMessage() {
		return this.$app.commonShareAppMessage()
	},
	onLoad(option) {
		this.user_id = option.user_id
		this.getSpriteInfo()
		this.getUserInfo()
	},

	methods: {

		//HTTP

		getUserInfo() {
			this.$app.request(this.$app.API.USER_INFO, {
				user_id: this.user_id
			}, res => {
				this.userInfo = res.data

				this.userInfo = {
					avatarurl: res.data['avatarurl'] || this.$app.AVATAR,
					nickname: res.data['nickname'] || this.$app.NICKNAME,
					id: res.data['id'] || null,
				}
			})
		},
		settle() {
			if (this.spriteInfo.earn < 100) {
				this.$app.toast('TA的能量太少了，稍后再来吧')
			} else {
				this.$app.request(this.$app.API.SPRITE_SETTLE, {
					user_id: this.user_id
				}, res => {
					if (this.user_id == 1) {
						this.spriteInfo.earn = 0
					} else {
						this.getSpriteInfo()

					}

					this.$app.toast('为TA收集能量，你获得:' + res.data+'能量')
					this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
						this.$app.setData('userCurrency', res.data)

					})
				}, 'POST', true)
			}
		},
		getSpriteInfo() {
			this.$app.request(this.$app.API.SPRITE_INFO, {
				user_id: this.user_id
			}, res => {
				this.spriteInfo = res.data


				this.$app.closeLoading(this)
			})
		}
	}
}
